.app-wrapper {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
    grid-template-areas:
            'h h'
            'n c';
}

.app-wrapper_content {
    grid-area: c;
    background-color: burlywood;
}